body,
input {
    /*font-family: "Lucida Console", Monaco, monospace;*/
    font-family: 'IBM Plex Mono', monospace;
    font-size: 16px;
    font-weight: 400;
}
.app-wrapper {
    padding:15px;
}
h2 {
    font-size: 1.25rem;
    font-weight: 600;
}
.btn-primary {
    color: #333;
    background-color: #f6f6f6;
    border-color: #ccc;
}
.btn-primary:hover {
    color: #333;
    background-color: #ddd;
    border-color: #aaa;
}
.btn-primary:focus,
.btn-primary.focus {
    color: #333;
    background-color: #ddd;
    border-color: #0062cc;
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5);
}
.btn-primary.disabled,
.btn-primary:disabled {
    color: #616161;
    background-color: #ddd;
    border-color: #ddd;
}
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show>.btn-primary.dropdown-toggle {
    color: #333;
    background-color: #ddd;
    border-color: #005cbf;
}
.btn-primary:not(:disabled):not(.disabled):active:focus,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.show>.btn-primary.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5);
}

.navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}
#dropdown-game-menu {
    margin-bottom:15px;
}
#dropdown-game-menu .dropdown-toggle::after {
    display: none;
}
#dropdown-game-menu h2 {
    display: inline;
    margin-right: .75rem;
}

.custom-game-instructions {
    max-width: 40.5rem;
    margin: 2rem auto;
    padding:1rem 1.5rem;
    background-color: #f9f9fb;
    border:1px solid #ddd;
    border-radius: 10px;
}

/* --------------- Dark mode --------------- */

body.dark-mode {
    background-color: #303030;
    color: #d6d6d6;
}

.dark-mode .dropdown-menu {
    background-color: #3b3b3b;
    color: #d6d6d6;
    box-shadow: 0 0 7px 0 rgba(0,0,0,0.25);
}
.dark-mode .btn-primary {
    color: #d6d6d6;
    background-color: #252525;
    border-color: #555;
}
.dark-mode .btn-primary:hover {
    color: #d6d6d6;
    background-color: #3b3b3b;
    border-color: #666;
}
.dark-mode .btn-primary:not(:disabled):not(.disabled):active,
.dark-mode .btn-primary:not(:disabled):not(.disabled).active,
.dark-mode .show>.btn-primary.dropdown-toggle {
    color: #d6d6d6;
    background-color: #252525;
    border-color: #555;
}
.dark-mode .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}
.dark-mode .form-control {
    background-color: #252525;
    color: #d6d6d6;
    border-color: #777;
}
.dark-mode .dropdown-divider {
    border-top-color: #777;
}

.dark-mode .custom-game-instructions {
    background-color: #2a2a2a;
    border:1px solid #444;
}