.number-pad {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(3, 1fr);
    grid-auto-columns: row;
    gap: .5rem;
    padding:.5rem;
    background-color: #e9e9e9;
    border:1px solid #fff;
    border-top: 0 none;
    border-left: 0 none;
    border-radius: 5px;
    box-shadow: 3px 3px 3px 0 rgba(0,0,0,0.15), -1px -1px 0 0 #d9d9d9;
    position: absolute;
    top:0;
    left:0;
    display:none;
    z-index: 1;
}

.number-pad.active {
    display: grid;
}

.number-pad button {
    width: 3rem;
    height: 3rem;
    font-size: 1.25rem;
    line-height: 1.5;
    padding:.25rem 0;
    background-color:#f6f6f6;
    background-image: linear-gradient(to bottom, #fff 0%, #fff 49%, #f4f4f4 51%, #f4f4f4 100%);
    color: #000;
    border:1px solid #fff;
    border-radius: 3px;
    box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.15);
}

.number-pad button:hover {
    background-color:#fff;
    background-image: none;
    box-shadow: 0 0 0 1px #bbb;
}

.number-pad button.clear-button,
.number-pad button.solve-button {
    grid-column-start: 1;
    grid-column-end: 4;
    width:auto;
}

/* --------------- Dark Mode --------------- */

.dark-mode .number-pad {
    background-color: #222;
    border:1px solid #444;
    box-shadow: 3px 3px 3px 0 rgba(0,0,0,0.5), -1px -1px 0 0 #000;
}

.dark-mode .number-pad button {
    background-color:#3d3d3d;
    background-image: linear-gradient(to bottom, #3a3a3a 0%, #3a3a3a 49%, #303030 51%, #303030 100%);
    border:1px solid #666;
    box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.5);
    color: #d6d6d6;
}
.dark-mode .number-pad button:hover {
    background-color:#3a3a3a;
    background-image: none;
    border-color: #303030;
    box-shadow: 0 0 0 1px #666;
}